
























import CdAgregatorSuspiciousTravelsItem from '../../../components/CDAgregatorTravels/CdAgregatorSuspiciousTravelsItem.vue'
import PageTitle from "../../../components/Page/PageTitle.vue";
import { Component, Vue } from "vue-property-decorator";
import useItemsPage from "@/compositions/useItemsPage";
import { useApiGetTravels } from "@/api/travel";
import { computed } from "@vue/composition-api";
import useRouter from "@/compositions/useRouter";
import { useApiDriverGetSuspicious } from "@/api/driver";

@Component({
  components: { PageTitle, CdAgregatorSuspiciousTravelsItem },

  setup() {
    const router = useRouter();
    const agregator = router.currentRoute.params.name;
    const {
      items,
      init,
      totalPages,
      page,
      nextPage,
      prevPage,
      showMore,
    } = useItemsPage({
      api: useApiDriverGetSuspicious,
    });
    const fetchData = computed(() => ({
      page: page.value,
      agregator,
    }));
    init({
      fetchData,
    });
    return { items, page, nextPage, prevPage, showMore, totalPages };
  },
})
export default class CDAgregatorSuspiciousTravels extends Vue {}
